@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&display=swap");
p, ol li,
ul li {
  font-family: "Oswald", Arial, Helvetica, sans-serif;
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Oswald", Arial, Helvetica, sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}

html {
  font-size: 62.5%;
}

* {
  border: none;
  box-sizing: border-box;
  outline: none;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

.site-inner {
  padding-top: 48px;
}
.site-inner .banner-ad img {
  display: inline-block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 12px;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  text-decoration: none;
  transition: all 0.2s ease;
}
p, ol li,
ul li {
  margin-top: 0;
  margin-bottom: 28px;
  font-size: 1.6rem;
  line-height: 170%;
}
p a, ol li a,
ul li a {
  transition: all 0.2s ease;
}
ol li,
ul li {
  margin-bottom: 8px;
}
ol li a,
ul li a {
  text-decoration: none;
  transition: all 0.2s ease;
}
.mediabase-chart-frame-container, .chartmetric-frame-container {
  height: 0;
  position: relative;
  padding-bottom: 38.25%;
}
.mediabase-chart-frame-container iframe, .chartmetric-frame-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.chartmetric-frame-container {
  margin-bottom: 48px;
  padding-bottom: 61.25%;
}

.site-header {
  padding-top: 8px;
  padding-bottom: 8px;
  background-color: #eee;
}
@media (min-width: 992px) {
  .site-header .toggle-mobile-menu {
    display: none;
  }
}
@media (min-width: 992px) {
  .site-header .offcanvas-header {
    display: none;
  }
}
.site-header .nav-item {
  text-transform: uppercase;
}
@media (min-width: 992px) {
  .site-header .nav-item {
    margin-bottom: 0;
  }
}

.site-footer {
  padding-top: 18px;
  padding-bottom: 18px;
  background-color: #000;
}
.site-footer p, .site-footer ol li, ol .site-footer li,
.site-footer ul li,
ul .site-footer li {
  color: #fff;
  font-size: 1.4rem;
  margin-bottom: 0;
}

.home .mediabase-chart-container, .home .chartmetric-container {
  padding-top: 48px;
  padding-bottom: 48px;
}
.home .chartmetric-container {
  background-color: #eee;
}